import { Button } from "@moai/core";
import { icons } from "@moai/icon";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { RemoveRowPayload } from "diagram/state/reducer/row/remove";
import React from "react";
import s from "./remove.module.scss";

interface Props {
    tableId: number;
    rowId: number;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const removeRow = (props: Props) => {
    const { tableId, rowId } = props;
    const payload: RemoveRowPayload = { tableId, rowId };
    props.diagramDispatch({ type: "remove-row", payload });
};

export const rowRemoveOnKeyDown = (props: Props) => (
    event: React.KeyboardEvent
): void => {
    const { key, target } = event;
    switch (key) {
        case "Backspace":
        case "Delete":
            if (target instanceof HTMLInputElement) return;
            removeRow(props);
            event.stopPropagation();
    }
};

export const RowRemoveButton = (props: Props) => (
    <div className={[s.container].join(" ")}>
        <Button
            onClick={() => void removeRow(props)}
            style={Button.style.flat}
            size={Button.size.small}
            icon={icons.remove}
        />
    </div>
);
