import { ButtonMenu, ButtonMenuItem } from "@moai/core";
import { icons } from "@moai/icon";
import { ImportRawPayload } from "diagram/state/reducer/import/raw";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";
import { HeaderChildProps } from "../../header";

interface Props extends HeaderChildProps {
    diagramDispatch: React.Dispatch<DiagramAction>;
}

type setIsBusy = React.Dispatch<React.SetStateAction<boolean>>;

const loadRemote = async (props: Props, setIsBusy: setIsBusy, url: string) => {
    setIsBusy(true);
    const raw = await fetch(url).then((response) => response.json());
    const payload: ImportRawPayload = { raw, isAppend: false };
    props.diagramDispatch({ type: "import-raw", payload });
    setIsBusy(false);
};

const getSampleItems = (props: Props, setIsBusy: setIsBusy): ButtonMenuItem[] =>
    [
        ["Import Airlines sample", "airlines"],
        ["Import IMDb sample", "imdb"],
        ["Import Employees sample", "employees"],
        "divider",
        ["Import sample 1 (3 small tables)", "1"],
        ["Import sample 2 (Table with many columns)", "2"],
        ["Import sample 3 (28 tables)", "3"],
        ["Import sample 4 (1710 tables)", "4"],
    ].map((item) => {
        if (item === "divider") return "divider";
        const [label, name] = item;
        const url = `https://diagramplus.com/samples/${name}.json`;
        return { label, fn: () => loadRemote(props, setIsBusy, url) };
    });

const getItems = (props: Props, setIsBusy: setIsBusy): ButtonMenuItem[] => [
    { label: "Import from a database (require TablePlus app)", disabled: true },
    "divider",
    ...getSampleItems(props, setIsBusy),
];

export const HeaderImport = (props: Props) => {
    const [isBusy, setIsBusy] = React.useState(false);
    return (
        <ButtonMenu
            button={{
                size: props.size.button,
                icon: icons.import,
                children: "Import",
                isBusy,
                title: "Import a diagram",
            }}
            items={getItems(props, setIsBusy)}
        />
    );
};
