import { EditEdge, EditTable } from "diagram/edit/edit";
import * as React from "react";
import { debugLog } from "utils/debug";
import { getEdgeBoxes } from "./box";
import { useEdgeObserve } from "./observe";
import { EdgePath, getEdgePath } from "./path";
import { getEdgePoints } from "./point";
import { EdgesMids } from "./split-x";
import { RowYMap } from "./split-y";

interface Props {
    edge: EditEdge;
    fromTable: EditTable;
    toTable: EditTable;
    // UI optimization. These can be null when getEdgeState is called from
    // environment where performance is crucial (like when dragging)
    edgesMids: React.MutableRefObject<EdgesMids> | null;
    rowYMap: React.MutableRefObject<RowYMap> | null;
}

export const getEdgeState = (props: Props): EdgePath => {
    debugLog("getEdgeState");
    const boxes = getEdgeBoxes(props);
    const edgesMids = props.edgesMids?.current ?? null;
    const rowYMap = props.rowYMap?.current ?? null;
    const { edge } = props;
    const points = getEdgePoints({ edgesMids, edge, boxes, rowYMap });
    const path = getEdgePath(points);
    return path;
};

export const useEdgeState = (props: Props): EdgePath | null => {
    const { edge, fromTable, toTable, edgesMids, rowYMap } = props;

    const changed = useEdgeObserve(edge);

    // "draw" calculation depends on the result of rendered tables so it can
    // not be calculated on edge's render but instead as an effect after
    const [state, setState] = React.useState<EdgePath | null>(null);
    const prev = React.useRef(0);
    React.useLayoutEffect(() => {
        window.clearTimeout(prev.current);
        // setTimeout to fix a bug in Firefox where styles are not applied
        // fully at the time when get element
        prev.current = window.setTimeout(() => {
            const props = { edge, fromTable, toTable, edgesMids, rowYMap };
            const newState = getEdgeState(props);
            setState(newState);
        }, 0);
        return () => window.clearTimeout(prev.current);
    }, [edge, fromTable, toTable, edgesMids, changed, rowYMap]);
    return state;
};
