import { ButtonMenu, ButtonMenuItem } from "@moai/core";
import { icons } from "@moai/icon";
import { EditDiagram } from "diagram/edit/edit";
import React from "react";
import { usePlatform } from "utils/platform";
import { HeaderChildProps } from "../../header";
import * as Image from "./image/image";
import * as Sql from "./sql/sql";

interface Props extends HeaderChildProps {
    diagram: EditDiagram;
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
}

const getImageItems = (props: Image.ExportImageProps): ButtonMenuItem[] => {
    const images: [string, Image.ExportImageFormat, number?][] = [
        ["Export as PNG (High quality)", "png", 2],
        ["Export as PNG (Low quality)", "png", 1],
        ["Export as SVG (Web page)", "svg"],
    ];
    return images.map(([label, format, quality]) => {
        const fn = () => Image.exportAsImage(props, format, quality);
        return { label, fn };
    });
};

const getSqlItems = (props: Sql.ExportSqlProps): ButtonMenuItem[] => {
    return Sql.exportSqlDrivers.map((driver) => ({
        label: `Export to ${driver}`,
        fn: () => Sql.exportAsSql(props, driver),
    }));
};

export const HeaderExport = (props: Props) => {
    const { diagram, setIsCapture } = props;
    const platform = usePlatform();
    return (
        <ButtonMenu
            items={[
                ...getImageItems({ platform, setIsCapture }),
                "divider",
                ...getSqlItems({ platform, diagram }),
            ]}
            button={{
                icon: icons.share,
                size: props.size.button,
                title: "Export",
            }}
        />
    );
};
