import domtoimage from "dom-to-image";
import * as React from "react";
// @ts-ignore
import html2canvas from "./html2canvas";
import { ExportImageFile, ExportImageFormat } from "./image";

interface Props {
    canvas: HTMLElement;
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
    format: ExportImageFormat;
    scale?: number;
}

const prefixes: Record<ExportImageFormat, string> = {
    svg: "data:image/svg+xml;charset=utf-8,",
    png: "data:image/png;base64,",
};

const getImageData = async (props: Props): Promise<string> => {
    switch (props.format) {
        case "png":
            const canvas = await html2canvas(props.canvas, {
                scale: props.scale,
            });
            return canvas.toDataURL();
        case "svg":
            // html2canvas is not supported SVG so we still need to use this
            // outdated library
            return await domtoimage.toSvg(props.canvas);
    }
};

const sleep = async (num: number): Promise<void> => {
    await new Promise((resolve) => window.setTimeout(() => resolve(), num));
};

export const getExportImageFile = async (
    props: Props
): Promise<ExportImageFile> => {
    try {
        // Prepare
        props.setIsCapture(true);
        await sleep(10); // Ensure that all are rendered in capture mode
        // Capture image
        const data = await getImageData(props);
        const prefix = prefixes[props.format];
        const body = data.replace(prefix, "");
        return { prefix, body };
    } catch (error) {
        // We handle error outside. The try catch here only to reset prepare
        // work such as capture mode and inlining styles
        throw error;
    } finally {
        props.setIsCapture(false);
    }
};
