import { Platform } from "utils/platform";
import { getExportImageFile } from "./get";
import { saveExportImageFile } from "./save";
import { fixSafari } from "./safari-fix";

fixSafari();

export type ExportImageFormat = "png" | "svg";

export interface ExportImageFile {
    prefix: string;
    body: string; // base64, without prefix
}

export interface ExportImageProps {
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
    platform: Platform;
}

export const exportAsImage = async (
    props: ExportImageProps,
    format: ExportImageFormat,
    scale?: number
): Promise<void> => {
    const { setIsCapture, platform } = props;
    const canvas = document.getElementById("canvas");
    if (canvas === null) throw Error("Canvas is null");
    const getOptions = { canvas, format, setIsCapture, scale };
    try {
        const file = await getExportImageFile(getOptions);
        saveExportImageFile({ file, format, platform });
    } catch (error) {
        if (format === "png" && scale === 2) {
            window.alert([
                "There was an error saving your diagram to PNG. ",
                "This is likely because the diagram is too large. ",
                'Please try again with the "low-quality" option.',
                platform === "web"
                    ? "\nYou can also try using another browser."
                    : "",
            ].join(""));
            return;
        }
        throw error;
    }
};
